.flex {
    display: flex;
    width: 100%;
    height: 100%;
}

.vCenter {
    flex-direction: column;
    align-items: center;
    .flex();
}

.hCenter {
    flex-direction: row;
    align-items: center;
    .flex();
}

.vFull {
    flex-direction: column;
    align-items: stretch;
    .flex();
}

.hFull {
    flex-direction: row;
    align-items: stretch;
    .flex();
}

.center {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    .flex();
}

.hWarp {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    .flex();
}