@import "../../style/index.less";

.button {
	outline: none;
	border: none;
	// display: inline-flex;
	// align-items: center;
	text-align: center;
	cursor: pointer;
	position: relative;
	color: inherit;
	transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out,
		color 0.2s ease-in-out, opacity 0.2s ease-in-out;

	svg {
		fill: currentColor;
		width: 1em;
		height: 1em;
	}

	.content {
		display: inline-flex;
		align-items: center;
	}

	&.loading {
		cursor: default;
		pointer-events: none;

		.content {
			visibility: hidden;
		}

		.spin {
			display: inline-block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-0.5em, -0.5em);
		}
	}
}

.spin {
	width: 1em;
	height: 1em;
	margin: 0;
	display: none;
}

.textContent {
	display: inline-block;
}

.block {
	width: 100%;
	justify-content: center;
}

.prefix {
	margin-right: 4px;
}

.suffix {
	margin-left: 4px;
}

.s {
	padding: 0 12px;
	height: 24px;
	font-size: @tip-text-size;
}

.m {
	padding: 0 18px;
	height: 28px;
	font-size: @text-size;
}

.l {
	padding: 0 28px;
	height: 32px;
	font-size: @importent-text-size;
}

.xl {
	padding: 0 36px;
	height: 40px;
	font-size: @h3-text-size;
}

.color(@color) {
	color: @color;
}

.contained {
	background-color: @background-color;
	border: solid 1px @background-color;
	border-radius: @border-radius;

	&:hover {
		background-color: @background-hover-color;
	}

	&:active {
		background-color: @background-active-color;
	}

	&.primary {
		background-color: @main-color;
		border-color: @main-color;
		.color(@background-base-color);

		&:hover {
			background-color: @main-hover-color;
			border-color: @main-hover-color;
		}

		&:active {
			background-color: @main-press-color;
			border-color: @main-press-color;
		}
	}

	&.danger {
		background-color: @error-color;
		border-color: @error-color;
		.color(@background-base-color);

		&:hover {
			background-color: @error-hover-color;
			border-color: @error-hover-color;
		}

		&:active {
			background-color: @error-press-color;
			border-color: @error-press-color;
		}
	}

	&[disabled] {
		background-color: @background-dark-color;
		border-color: @border-color;
		cursor: not-allowed;
		.color(@text-tip-color);

		&:hover {
			background-color: @background-dark-color;
			border-color: @border-color;
		}

		&:active {
			background-color: @background-dark-color;
			border-color: @border-color;
		}
	}
}

.solid {
