		}
	}
}

.link {
	background-color: transparent;

	&.s,
	&.m,
	&.l,
	&.xl {
		padding: 0;
		font-size: inherit;
		height: initial;
	}

	.color(@text-link-color);

	&:hover {
		opacity: 0.8;
	}

	&:active {
		opacity: 1;
	}

	&.primary {
		.color(@main-color);

		&:hover {
			.color(@main-hover-color);
		}

		&:active {
			.color(@main-press-color; );
		}
	}

	&.danger {
		.color(@error-color);

		&:hover {
			.color(@error-hover-color);
		}

		&:active {
			.color(@error-press-color);
		}
	}

	&[disabled] {
		opacity: 0.3;
		cursor: not-allowed;

		&:hover {
			opacity: 0.3;
		}

		&:active {
			opacity: 0.3;
		}
	}
}

.text {
