
.container {
    background-color: #f0f0f5;
    border-radius: 2px;
    border: solid 2px #f0f0f5;
    display: inline-block;
    .m {
        height: 30px;
        line-height: 30px;
    }
    .s {
        height: 20px;
        line-height: 20px;
    }
    .l {
        height: 40px;
        line-height: 40px;
    }
}
.item {
    display: inline-block;
    cursor: pointer;
    padding: 0 24px;
    border-radius: 2px;
    color: #93959e;
    height: 30px;
    line-height: 30px;
    transition: color ease-in-out .2s;
}
.active {
    background: #fff;
    border: 2px;
    color: #0f0f0f;
}

.notActive {
    &:hover {
        color: #00c735;
    }
}

.disableContainer {
   cursor: not-allowed;
//    pointer-events: none;
   .item {
       cursor: not-allowed;
   }
   .active {
      color: #aeafb5
   }
   .notActive {
    &:hover {
        color: #93959e;
    }
}
}