/** 主题色 */

// 主题色-普通状态
@main-color: #00b831;
// 主题色-鼠标悬浮状态
@main-hover-color: #00c735;
// 主题色-鼠标按下状态
@main-press-color: #00a82d;
// 辅助色-与主色并列展示时使用
@assist-color: #2f85f9;

/** 文字色 */

// 主文字色
@text-color: #0f0f0f;
// 链接文字
@text-link-color: #33508f;
// 突出文字
@text-important-color: #4a4f54;
// 次级突出文字
@text-sub-important-color: #6c737b;
// 辅助文字
@text-tip-color: #93959e;
// 次级辅助文字
@text-sub-tip-color: #aeafb5;

/** 线框色 */

// 主边框
@border-color: #d7d9db;
// 分割线
@divider-color: #ebedf0;
// 框架分割线
@divider-area-color: #e6e7eb;

/** 图标色 */

// 指示类图标色
@icon-tips-color: #b0b7c3;

// 操作类图标色
@icon-action-color: #9d9fa9;

/** 滚动条颜色 */
// 默认状态
@scrollbar-color: #e1e6eb;
// 鼠标悬浮或按下状态
@scrollbar-active-color: #d3dae0;

/** 背景色 */

// 背景主色
@background-color: #f6f8fa;
// 背景基色
@background-base-color: #fff;
// 背景深色
@background-dark-color: #f0f0f5;
// 悬停背景色
@background-hover-color: #f3f3f6;
// 选中背景色
@background-active-color: #ebebf0;
// 文字选取背景色
@background-select-color: #cde2fe;

/** 功能色 */

// 警告色
@warning-color: #ff8a05;
// 错误色
@error-color: #ff534e;
// 错误色悬浮状态
@error-hover-color: #ff7571;
// 错误色按下状态
@error-press-color: #f5504b;

/** 字体大小 */

// 主标题
@h1-text-size: 24px;

// 普通标题
@h2-text-size: 18px;

// 普通标题
@h3-text-size: 16px;

// 突出内容
@importent-text-size: 14px;

// 正文
@text-size: 13px;

// 说明文字
@tip-text-size: 12px;

/** 圆角 **/

@border-radius: 2px;

section {
    // margin-bottom: 30px;
    padding-bottom: 30px;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #fff;
}
::-webkit-scrollbar-thumb {
    background-color: #e1e6eb;
    border: solid 2px #fff;
    border-radius: 6px;
}