@import "../../style/index.less";

.h1 {
	font-size: @h1-text-size;
	line-height: 1.5;
}

.h2 {
	font-size: @h2-text-size;
	line-height: 1.5;
}

.h3 {
	font-size: @h3-text-size;
	line-height: 1.5;
}

.main {
	font-size: @importent-text-size;
	line-height: 1.5;
}

.text {
	font-size: @text-size;
	line-height: 1.5;
}

.tips {
	font-size: @tip-text-size;
	line-height: 1.5;
}

.ellipsis {
	width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.pre {
	white-space: pre;
}
