@import "../../style/index.less";

.code {
	font-size: @text-size;
	border-radius: @border-radius;
	overflow: auto;
	background: #2d2d2d;
	padding: 20px;
	button {
		background: #2d2d2d;
		color: #ccc;
		/* outline: none; */
		border: none;
		padding: 0;
		margin: 0;
	}
	> code {
		text-shadow: none;
		color: #ccc;
		background: none;
		font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
		font-size: 1em;
		text-align: left;
		white-space: pre;
		word-spacing: normal;
		word-break: normal;
		word-wrap: normal;
		line-height: 1.5;
		-moz-tab-size: 4;
		-o-tab-size: 4;
		tab-size: 4;
		-webkit-hyphens: none;
		-ms-hyphens: none;
		hyphens: none;

		* {
			font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono",
				monospace, "Microsoft Yahei" !important;
		}

		:global(.token.operator) {
			background: inherit;
		}
	}
}
